.contacts {
    padding: 50px 0;
    border-top: 1px solid var(--iconColor);
    &-list {
        font-weight: 500;
        line-height: 1.2;
        li {
            display: flex;
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }
        i {
            width: 20px;
            margin-right: 8px;
            color: var(--brandColor);
            font-size: 20px;
            line-height: 1;
        }
        .icon-1-3_mail {
            font-size: 17px;
        }
        .icon-1-0_location {
            font-size: 22px;
        }
        p {
            margin: 0;
        }
        a {
            display: block;
        }
    }
    &__info {
        max-width: 25%;
        width: 100%;
        ul {
            margin-top: 30px;
        }
    }
    &__map {
        max-width: 75%;
        width: 100%;
        height: 450px;
        & > div {
            width: 100%;
            height: 100%;
        }
    }
}
@media(max-width: 991px) {
    .contacts {
        &__info {
            max-width: 40%;
        }
        &__map {
            max-width: 60%;
            height: 400px;
        }
    }
}
@media(max-width: 767px) {
    .contacts {
        &__info {
            max-width: 100%;
            margin-top: 0;
        }
        &__map {
            max-width: 100%;
            height: 300px;
            margin-top: 30px;
        }
    }
}
@media(max-width: 575px) {
    .contacts {
        &-list {
            font-size: 15px;
        }
    }
}